body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    box-sizing: border-box;
}
html{
    min-width: 550px;
    overflow: auto;
}

.body-styles {
    background-color: rgb(0, 0, 0);
    color: cornsilk;
}

.sticky {
    position: fixed;
    top: 0;
}
header {
    width:100%;
    height:60px;
    font-size:1.4em;
    display:flex;
    align-items:center;
    justify-content:left;
  }

.active{
    
    font-family: 'Oxygen', sans-serif;
    z-index: 5;
    color: aqua;
}

#contactInfo  span  h3{
    font-family: 'Gothic A1', sans-serif;
    z-index: -5;
}

.contact-image {
    max-width: 25%;
    max-height: 25%;
}

#filesText {
    padding: 1cm;
}
#personal-letter {
    margin-top: 20px;
    text-align: center;
    justify-content: center;
}

#cv-text {
    text-align: center;
}
#cv-text > h2 {
    padding: 10px;
}


#projectlist img {
    max-height: 6cm;

}

/* a bit more responsive to the different sizes of screens */

@media screen and (max-width: 400px) {
  .grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    align-items: center;
  }  
}

@media screen and (min-width: 400px) {
  .grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    align-items: center;
  }
}

  .grid-item {
    padding: 20px;
    text-align: center;
  }

  #navMenu {
    justify-content:right;
}
#navMenu a{
    
    font-family: 'Oxygen', sans-serif;
    z-index: 5;
}
.dropbtn {
    background-color: #6d4e0d;
    color: white;
    padding: 16px;
    font-size: 16px;
    border: none;
    cursor: pointer;
  }
  .dropbtn:hover, .dropbtn:focus {
    background-color: #2980B9;
  }
  
  .navMenu {
    position: relative;
    display: inline-block;
  }

  .navMenu-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  .navMenu-content a { 
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  .navMenu-content a:hover {background-color: #ddd;}

  .modalBackground {
    width: 100vw;
    height: 100vh;
    background-color: rgb(49, 47, 47);
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modalContainer {
    width: 75%;
    height: 50%;
    border-radius: 12px;
    background-color: rgb(20, 20, 20);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    display: flex;
    flex-direction: column;
    padding: 25px;
  }
  
  .modalContainer .title {
    display: inline-block;
    text-align: center;
    margin-top: 10px;
  }
  
  .titleCloseBtn {
    display: flex;
    justify-content: flex-end;
  }
  
  .titleCloseBtn button {
    background-color: transparent;
    border: none;
    font-size: 25px;
    cursor: pointer;
  }
  
  .modalContainer .body {
    flex: 50% 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.7rem;
    text-align: center;
  }
  
  .modalContainer .footer {
    flex: 20% 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modalContainer .footer button {
    width: 150px;
    height: 45px;
    margin: 10px;
    border: none;
    background-color: cornflowerblue;
    color: white;
    border-radius: 8px;
    font-size: 20px;
    cursor: pointer;
  }

  #grumpyCat{
    height: 250px;
  }

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

